import React from "react";
import { IAppIco } from "../../types";
import { _strokeCommonProps } from "../SVGFiles";

export const ArrowUpIco: React.FC<IAppIco> = ({ asset, stroke, height, width, className }) => {
    const _classList = `${className || ""} ss-app-config-ico-` + asset;

    return (
        <svg className={`stroke-ico ${_classList}`} width={width || "24"} height={height || "24"} viewBox="0 0 24 24" fill="none">
            <path d="M5 15.5L12 8.5L19 15.5" stroke={stroke || "currentColor"} {..._strokeCommonProps} strokeWidth={"2"} />
        </svg>
    );
};
